<template>
  <div class="container mt-3">
    <form action="#">
      <div class="row">
        <div class="col-md-offset-2">
          <div class="form-group col-md-6">
            <label class="control-label">Current password</label>
            <div class="input-icon">
              <i class="fa fa-lock fa-fw"></i>
              <input class="form-control" type="password" name="password" v-model="changePassForm.current_pass"
                     placeholder="Current password"
                     @blur="validate('current_pass')"
                     @keypress="validate('current_pass')">

            </div>
            <p class="errors" v-if="!!errorsChange.current_pass">{{ errorsChange.current_pass }}</p>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-md-offset-2">
          <div class="form-group col-md-6">
            <label class="control-label">New password</label>
            <div class="input-icon">
              <i class="fa fa-lock fa-fw"></i>
              <input class="form-control" type="password" name="new_password" v-model="changePassForm.new_pass"
                     placeholder="New password"
                     @blur="validate('new_pass')"
                     @keypress="validate('new_pass')">
            </div>
            <p class="errors" v-if="!!errorsChange.new_pass">{{ errorsChange.new_pass }}</p>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-md-offset-2">
          <div class="form-group col-md-6">
            <label class="control-label">Re-type password</label>
            <div class="input-icon">
              <i class="fa fa-lock fa-fw"></i>
              <input class="form-control" type="password" name="re_new_password" v-model="changePassForm.re_new_pass"
                     placeholder="Re type password"
                     @blur="validate('re_new_pass')"
                     @keypress="validate('re_new_pass')">
            </div>
            <p class="errors" v-if="!!errorsChange.re_new_pass">{{ errorsChange.re_new_pass }}</p>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-md-offset-2">
          <div class="form-group col-md-6">
            <a href="javascript:;" class="btn green-haze" @click="handleChangePassword">
              Change Password </a>
          </div>
        </div>
      </div>

    </form>
  </div>
</template>

<script>
import {profileService} from "@/api/profile/profile.service";
import {ChangePassSchema} from "@/views/users/js/validate";

export default {
  name: "UserChangePass",
  data() {
    return {
      changePassForm: {
        current_pass: '',
        new_pass: '',
        re_new_pass: '',
      },
      errorsChange: {
        first_name: '',
        last_name: '',
        email: '',
        password: '',
        confirm_password: '',
        term_condition: ''
      },
    }
  },
  methods: {
    /**
     * Change pass
     */
    handleChangePassword() {
      ChangePassSchema.validate(this.changePassForm, {abortEarly: false})
          .then(() => {
            profileService.changePasswordUser(this.changePassForm.current_pass, this.changePassForm.new_pass)
                .then(response => {
                  const data = response.data
                  let status = data["status"];
                  let classError = '';
                  if (status) {
                    classError = 'alert-success'
                  } else {
                    classError = 'alert-danger'
                  }
                  this.$emit('info-update-user', data["message"], classError)
                })

          })
          .catch((err) => {
            err.inner.forEach((error) => {
              this.errorsChange = {...this.errorsChange, [error.path]: error.message};
            });
          });

    },
    validate(field) {
      ChangePassSchema.validateAt(field, this.user)
          .then(() => (this.errorsChange[field] = ""))
          .catch((err) => {
            this.errorsChange[err.path] = err.message;
          });
    }
  }
}
</script>

<style lang="scss" scoped>


</style>
