import configs from "@/helpers/configs";
import axios from 'axios';

export const userService = {
    login,
    register,
    logout,
    resetPassword
}

function login(username, password) {
    let bodyFormData = new FormData();
    bodyFormData.append("username", username)
    bodyFormData.append("password", password)
    return axios({
        method: 'POST',
        timeout: 1000 * 60,
        url: configs.apiUrl + 'login',
        headers: {
            'Content-Type': 'multipart/form-data',
        },
        data: bodyFormData
    }).then(response => {
        let data = response.data;
        if (data["status"]) {
            const objectUser = {
                "token": data["data"].token,
                "expiresIn": data["data"].expiresIn
            }
            localStorage.setItem('user', JSON.stringify(objectUser));
        }
        return response
    });
}

function logout() {
    // remove user from local storage to log user out
    localStorage.removeItem('user');
}

function register(user) {
    let bodyFormData = new FormData();
    bodyFormData.append("first_name", user.first_name)
    bodyFormData.append("last_name", user.last_name)
    bodyFormData.append("email", user.email)
    bodyFormData.append("password", user.password)
    bodyFormData.append("confirm_password", user.confirm_password)
    bodyFormData.append("term_condition", user.term_condition)
    return axios({
        method: 'post',
        timeout: 1000 * 60,
        url: configs.apiUrl + 'register',
        headers: {
            'Content-Type': 'multipart/form-data',
        },
        data: bodyFormData

    }).then(response => {
        console.log("register response:")
        console.log(response.data)
        return response
    });
}

function resetPassword(email) {
    let bodyFormData = new FormData();
    bodyFormData.append("email", email)
    return axios({
        method: 'post',
        timeout: 1000 * 60,
        url: configs.apiUrl + 'user/reset-password',
        headers: {
            'Content-Type': 'multipart/form-data',
        },
        data: bodyFormData

    }).then(response => {
        console.log("reset pass response:")
        console.log(response.data)
        return response
    });
}
