import configs from "@/helpers/configs";
import {authHeader} from "@/helpers/auth-header";
import axios from 'axios';

axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*';

export const profileService = {
    infoUser,
    updateUser,
    changePasswordUser
}

function infoUser() {
    return axios({
        method: 'GET', url: configs.apiUrl + "user/info", headers: authHeader(), data: {}
    })
}

function updateUser(user) {
    let bodyFormData = new FormData();
    bodyFormData.append("first_name", user.first_name);
    bodyFormData.append("last_name", user.last_name);
    bodyFormData.append("email", user.email);

    return axios({
        method: 'POST',
        timeout: 1000 * 60,
        url: configs.apiUrl + "user/update",
        headers: authHeader(),
        data: bodyFormData
    })
}

function changePasswordUser(currentPass, newPass) {
    let bodyFormData = new FormData();
    bodyFormData.append("current_pass", currentPass);
    bodyFormData.append("new_pass", newPass)

    return axios({
        method: 'POST',
        timeout: 1000 * 60,
        url: configs.apiUrl + "user/change-password",
        headers: authHeader(),
        data: bodyFormData
    })
}
