import LandingLayout from "@/views/landing/Layout";
import LandingDetail from "@/views/landing/Detail";
import LandingCreate from "@/views/landing/Create"


const landingRouter = {
    path: '/landing',
    name: "landing",
    component: LandingLayout,
    meta: {
        title: 'Landing'
    },
    redirect: {name: 'landing.detail'},
    children: [
        {
            path: '', name: 'landing.detail', component: LandingDetail
        },
        {
            path: 'create', name: 'landing.create', component: LandingCreate, meta: {title : 'New Landing'}
        },
        {
            path: ':id/edit', name: 'landing.edit', component: LandingCreate,  meta: {title : 'Update Landing'}
        }
    ]
}

export default landingRouter;