<template>
  <div class="row">
    <form role="form">
      <div class="col-md-2">
        <div id="reportrange"
             style="background: #fff; cursor: pointer; padding: 5px 10px; border: 1px solid #ccc; width: 100%">
          <i class="fa fa-calendar"></i>&nbsp;
          <span  id="value_date"></span> <i class="fa fa-caret-down"></i>
        </div>
      </div>
      <div class="col-md-2">
        <el-input v-model="formSearch.title" placeholder="Title" size="small"></el-input>
      </div>
      <div class="col-md-2">
        <el-select v-model="formSearch.type"  @change="handleSubmitFilter"
                   placeholder="Please select type" size="small">
          <el-option label="PreLanding" value="1" ></el-option>
          <el-option label="Landing" value="2" ></el-option>
        </el-select>
      </div>
      <div class="col-md-2">
<!--        <select class="form-control" v-model="formSearch.status">-->
<!--          <option value="">-&#45;&#45;Select Status-&#45;&#45;</option>-->
<!--          <option v-bind:value="1">Active</option>-->
<!--          <option v-bind:value="3">Inactive</option>-->
<!--        </select>-->
        <el-select v-model="formSearch.status" @change="handleSubmitFilter"
                   placeholder="Please select status"  size="small">
          <el-option label="Active" value="1" ></el-option>
          <el-option label="Inactive" value="3" ></el-option>
        </el-select>
      </div>

      <div class="col-md-offset-8">
        <button type="submit" class="btn blue" @click.prevent="handleSubmitFilter">Search</button>
      </div>
    </form>

  </div>
</template>

<script>

export default {
  name: "FormSearch",
  data() {
    return {
      formSearch: {
        title: '',
        type: '',
        status: '',
      }
    }
  },
  mounted() {
    // eslint-disable-next-line no-undef
    ComponentsPickers.init()

  },
  methods: {
    handleSubmitFilter() {
      let dateString =document.getElementById("value_date").innerHTML;
      let arrayDate = dateString.split("to")
      let startDate = arrayDate[0].trim()
      let endDate = arrayDate[1].trim()
      this.$emit('update:start_date', startDate)
      this.$emit('update:end_date', endDate)
      this.$emit('update:title', this.formSearch.title);
      this.$emit('update:type', this.formSearch.type);
      this.$emit('update:status', this.formSearch.status);
      this.$emit('completed', {});
    }
  }
}
</script>

