<template>
  <div>
    <div class="row margin-top-20">
      <div class="col-md-12">
        <div class="portlet light bordered">
          <div class="portlet-title">
            <div class="caption font-red-sunglo">
              <i class="icon-settings font-red-sunglo"></i>
              <span class="caption-subject bold uppercase"> Offer source</span>
            </div>
          </div>
          <div class="portlet-title">
            <form-action :sourceId="this.listIdOfferSource.length > 0 ? parseInt(this.listIdOfferSource[0]): -1"
                         :listSourceId="this.listIdOfferSource"
                         :activeMultiple="activeMultiple" :activeOne="activeOne"
                         @completed="handleListOfferSource"/>
          </div>
          <div class="portlet-title">
            <form-search :title.sync="listQuery.title"
                         :start_date.sync="listQuery.start_date"
                         :end_date.sync="listQuery.end_date"
                         @completed="handleListOfferSource"/>

          </div>
          <div class="loader" v-if="loading"></div>
          <form-data :offerSources="offerSources" @handle-source-select="handleOfferSourceSelect"
                     :sort_field.sync="listQuery.sort_field"
                     :sort_type.sync="listQuery.sort_type"
                     @completed="handleListOfferSource"/>
        </div>
      </div>

    </div>

    <div class="card-body">
      <pagination v-show="totalOfferSource>0" :total="totalOfferSource" :page.sync="listQuery.page"
                  :limit.sync="listQuery.per_page"
                  @pagination="handleListOfferSource"/>
    </div>
  </div>
</template>

<script>
import Pagination from "@/components/Pagination";
import FormSearch from "@/views/offer-source/partials/FormSearch";
import FormAction from "@/views/offer-source/partials/FormAction";
import FormData from "@/views/offer-source/partials/FormData";
import {offerSourceService} from "@/api/offer-source/offer-source.service";

export default {
  name: "OfferSourceDetail",
  components: {
    Pagination,
    FormSearch,
    FormAction,
    FormData
  },
  data() {
    return {
      activeOne: 'disabled',
      activeMultiple: 'disabled',
      loading: false,
      listQuery: {
        page: 1,
        per_page: 20,
        date: '',
        sort_field: 'id',
        sort_type: 'DESC',
        title: '',
        start_date: '',
        end_date: ''
      },
      totalOfferSource: 0,
      listIdOfferSource: [],
      offerSources: null,
    }
  },
  created() {
    this.handleListOfferSource()
  },
  methods: {
    handleListOfferSource() {
      this.loading = true;
      offerSourceService.getListOfferSource(this.listQuery).then(response => {
        let data = response.data;
        this.offerSources = data["data"];
        console.log("offer sources...", this.offerSources)
        this.totalOfferSource = parseInt(data["total"]);
        this.loading = false
      })
    },
    handleOfferSourceSelect(objectSelect) {
      this.listIdOfferSource = [];
      this.activeOne = objectSelect.activeOne;
      this.activeMultiple = objectSelect.activeMultiple;
      const listOfferSource = objectSelect.listIdOfferSource;
      listOfferSource.forEach((element) => {
        this.listIdOfferSource.push(element.id);
      })
    }

  }
}
</script>

<style lang="scss" scoped>
</style>
