<template>
  <div class="register">
    <div class="content">
      <form class="register-form" style="display: block;" @submit.prevent="handleSubmit">
        <h3>Sign Up</h3>
        <p class="hint">
          Enter your personal details below:
        </p>
        <div class="form-group">
          <label class="control-label visible-ie8 visible-ie9">Fist Name</label>
          <div class="input-icon">
            <i class="fa fa-font"></i>
            <input class="form-control placeholder-no-fix" type="text"
                   placeholder="Fist Name"
                   name="first_name" v-model="user.first_name"
                   @blur="validate('first_name')"
                   @keypress="validate('first_name')">
          </div>
          <p class="errors" v-if="!!errorsUser.first_name">{{ errorsUser.first_name }}</p>
        </div>
        <div class="form-group">
          <label class="control-label visible-ie8 visible-ie9">Fist Name</label>
          <div class="input-icon">
            <i class="fa fa-check"></i>
            <input class="form-control placeholder-no-fix" type="text"
                   placeholder="Last Name" name="last_name" v-model="user.last_name"
                   @blur="validate('last_name')"
                   @keypress="validate('last_name')">
          </div>

          <p class="errors" v-if="!!errorsUser.last_name">{{ errorsUser.last_name }}</p>
        </div>
        <div class="form-group">
          <!--ie8, ie9 does not support html5 placeholder, so we just show field title for that-->
          <label class="control-label visible-ie8 visible-ie9">Email</label>
          <div class="input-icon">
            <i class="fa fa-envelope"></i>
            <input class="form-control placeholder-no-fix" type="text"
                   placeholder="Email" name="email" v-model="user.email"
                   @blur="validate('email')"
                   @keypress="validate('email')">
          </div>
          <p class="errors" v-if="!!errorsUser.email">{{ errorsUser.email }}</p>
        </div>
        <div class="form-group">

          <label class="control-label visible-ie8 visible-ie9">Password</label>
          <div class="input-icon">
            <i class="fa fa fa-lock"></i>
            <input class="form-control placeholder-no-fix" type="password"
                   autocomplete="off" id="register_password"
                   placeholder="Password" name="password" v-model="user.password"
                   @blur="validate('password')"
                   @keypress="validate('password')"
            >
          </div>
          <p class="errors" v-if="!!errorsUser.password">{{ errorsUser.password }}</p>
        </div>
        <div class="form-group">
          <label class="control-label visible-ie8 visible-ie9">Re-type Your Password</label>
          <div class="input-icon">
            <i class="fa fa-check"></i>
            <input class="form-control placeholder-no-fix" type="password" autocomplete="off"
                   placeholder="Re-type Your Password" name="confirm_password" v-model="user.confirm_password"
                   @blur="validate('confirm_password')"
                   @keypress="validate('confirm_password')">
          </div>
          <p class="errors" v-if="!!errorsUser.confirm_password">{{ errorsUser.confirm_password }}</p>
        </div>
        <div class="form-group margin-top-20 margin-bottom-20">
          <label class="check">
            <span><input type="checkbox" name="term_condition" value="1" v-model="user.term_condition"
            ></span>
            I agree to the <a href="javascript:;">
            Terms of Service </a>
            &amp; <a href="javascript:;">
            Privacy Policy </a>
          </label>
          <p class="errors" v-if="!!errorsUser.term_condition">{{ errorsUser.term_condition }}</p>
        </div>
        <div class="form-actions">
          <button type="button" id="register-back-btn" class="btn btn-default" @click="$router.go(-1)">Back</button>
          <button type="submit" id="register-submit-btn" class="btn btn-success uppercase pull-right">Submit</button>
        </div>
      </form>
      <!-- END REGISTRATION FORM -->
    </div>

  </div>
</template>

<script>
import {mapActions} from 'vuex'
import {RegisterSchema} from './js/validate'


export default {
  name: "AuthRegister",
  components: {},
  data() {
    return {
      errValidRePass: false,
      user: {
        first_name: '',
        last_name: '',
        email: '',
        password: '',
        confirm_password: '',
        term_condition: ''
      },
      errorsUser: {
        first_name: '',
        last_name: '',
        email: '',
        password: '',
        confirm_password: '',
        term_condition: ''
      },
    }
  },

  methods: {
    ...mapActions('account', ['register']),
    handleSubmit() {
      RegisterSchema.validate(this.user, {abortEarly: false})
          .then(() => {
            this.register(this.user)
          })
          .catch((err) => {
            err.inner.forEach((error) => {
              this.errorsUser = {...this.errorsUser, [error.path]: error.message};
            });
          });
    },
    validate(field) {
      RegisterSchema.validateAt(field, this.user)
          .then(() => (this.errorsUser[field] = ""))
          .catch((err) => {
            this.errorsUser[err.path] = err.message;
          });
    }
  }
}
</script>

<style lang="scss" scoped>


body {
  color: #333333;
  font-family: "Open Sans", sans-serif;
  padding: 0px !important;
  margin: 0px !important;
  font-size: 13px;
  direction: ltr;
}
.errors {
  color: red;
}

.is-danger {
  color: #f22435;
}

/* do not group these rules */
*::-webkit-input-placeholder {
  color: #8290a3;
}

*:-moz-placeholder {
  /* FF 4-18 */
  color: #8290a3;
  opacity: 1;
}

*::-moz-placeholder {
  /* FF 19+ */
  color: #8290a3;
  opacity: 1;
}

*:-ms-input-placeholder {
  /* IE 10+ */
  color: #8290a3;
}

*::-ms-input-placeholder {
  /* Microsoft Edge */
  color: #8290a3;
}

*::placeholder {
  /* modern browser */
  color: #8290a3;
}

.register {
  background-color: #364150 !important;

  .content {
    background-color: #eceef1;
    -webkit-border-radius: 7px;
    -moz-border-radius: 7px;
    border-radius: 7px;
    width: 400px;
    margin: 40px auto 10px auto;
    padding: 10px 30px 30px;
    overflow: hidden;
    position: relative;

    h3 {
      color: #4db3a5;
      text-align: center;
      font-size: 28px;
      font-weight: 400 !important;
    }

    .hint {
      color: #999;
      padding: 0;
      margin: 15px 0 7px 0;
    }
    .check {
      color: #8290a3;
      font-weight: 500;
    }
    .form-control {
      background-color: #dde3ec;
      height: 43px;
      color: #8290a3;
    }

    .form-actions {

      .btn-default {
        font-weight: 600;
        padding: 10px 25px !important;
        color: #6c7a8d;
        background-color: #ffffff;
        border: none;
      }

      .btn-default:hover {
        background-color: #fafaff;
        color: #45b6af;
      }
      .btn-success {
        background-color: #3b9c96;
        font-weight: 600;
        padding: 10px 20px !important;
      }
    }
  }
}

</style>
