<template>
  <div class="login">
    <div class="content">
      <HeaderError :error-class="getErrorType" :error-message="getErrorMessage"/>
      <!-- BEGIN LOGIN FORM -->
      <el-form :model="loginForm" :rules="rulesLogin" ref="loginForm" class="demo-ruleForm">
        <h3 class="form-title">Sign In</h3>
        <div class="alert alert-danger display-hide">
          <button class="close" data-close="alert"></button>
          <span>
			Enter any username and password. </span>
        </div>
        <div class="form-group">
          <el-form-item prop="username">
            <el-input type="text" autocomplete="off"
                      placeholder="Username" name="username" v-model="loginForm.username" autofocus/>
          </el-form-item>

        </div>
        <div class="form-group">
          <el-form-item prop="password">
            <el-input type="password" placeholder="Password" name="password" v-model="loginForm.password"/>
          </el-form-item>
        </div>
        <div class="form-actions">
          <el-button type="primary" @click="handleLogin('loginForm')">Login</el-button>
          <router-link :to="{name: 'forgot'}" id="forget-password" class="forget-password"> Forgot Password?
          </router-link>
          <!--          <a href="/forgot" id="forget-password" class="forget-password">Forgot Password?</a>-->
        </div>
        <div class="create-account">
          <p>
            <router-link :to="{name: 'register'}" class="uppercase"> Create an account</router-link>
          </p>
        </div>
      </el-form>
      <!-- END LOGIN FORM -->
    </div>
  </div>

</template>

<script>
import {mapState, mapActions, mapGetters} from 'vuex'
import HeaderError from "@/components/HeaderError";

export default {
  name: "AuthLogin",
  components: {
    HeaderError
  },
  data() {
    return {
      loading: false,
      loginForm: {
        username: '',
        password: '',
      },
      rulesLogin: {
        username: [
          {required: true, message: 'Please input username', trigger: 'blur'},
        ],
        password: [
          {required: true, message: 'Please input password', trigger: 'blur'},
        ]
      }
    }
  },
  computed: {
    ...mapState('account', ['status']),
    ...mapGetters('alert', ['getErrorType', 'getErrorMessage', 'getLoading']),


  },
  beforeCreate() {
    console.log("before...", )
  },
  created() {
    // reset login status
    console.log("LOG OUT")
    this.logout();
  },
  methods: {
    ...mapActions('account', ['login', 'logout']),
    handleLogin(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const {username, password} = this.loginForm;
          console.log(valid)
          this.login({username, password})
          console.log("final")
        }
      });

    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../styles/login.scss";
@import "../../styles/loader.scss";
</style>
