<template>
  <div>
    <transition name="fade-transform" mode="out-in">
      <component :is="layout">
        <router-view></router-view>
      </component>
    </transition>
  </div>
</template>

<script>
const default_layout = "default";

export default {
  computed: {
    layout() {
      return (this.$route.meta.layout || default_layout) + '-layout';
    }
  }
}
</script>

