  -webkit-text-size-adjust: 100%;
}

body {
  background-color: #364150;
  font-family: "Roboto", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 13px;
}

.swal2-popup {
  font-size: 15px !important;
}

.errors {
  color: red;
}
</style>
