import * as Yup from "yup";

export const ChangePassSchema = Yup.object().shape({
    current_pass: Yup.string()
        .required('Current pass is required'),
    new_pass: Yup.string()
        .required('New pass is require'),
    re_new_pass: Yup.string()
        .oneOf([Yup.ref('new_pass'), null], "Password must match")
})
