<template>
  <div>
    <div class="loader" v-if="campaigns == null && campaigns.length < 0">
    </div>
    <el-table
        :data="campaigns"
        :header-cell-style="{ background: '#f5f7fa', color:'black',textAlign: 'center',    fontWeight: 'normal' }"
        height="600"
        :header-row-style="{height:'55px'}"
        border
        style="width: 100%; margin-top: 20px"
        @cell-mouse-enter="mouseOver"
        @cell-mouse-leave="mouseLeave">
      <el-table-column
          type="selection"
          width="30"
          align="center">
      </el-table-column>
      <el-table-column
          prop="title"
          label="Title"
          align="left">
        <template slot-scope="scope">
          <div class="panel-action position-relative">
            <div class="name"> {{ scope.row.title }}</div>
            <ul class="position-absolute right-0 top-1 display"
                v-show="buttonIndex === scope.row.id">
              <li>
                <a href="javascript:void(0)">
                  <i class="fa fa-edit blue"></i>
                </a>
              </li>
              <li>
                <a href="javascript:void(0)">
                  <i class="fa fa-trash blue"></i>
                </a>
              </li>
            </ul>
          </div>
        </template>
      </el-table-column>
      <el-table-column
          prop="clicks"
          label="Clicks"
          align="right"
          width="60"
          sortable>
      </el-table-column>
      <el-table-column
          prop="lp_clicks"
          label="LP Clicks"
          width="100"
          align="right"
          sortable>
      </el-table-column>
      <el-table-column
          prop="payout"
          label="Payout"
          align="right"
          width="80"
          sortable>
      </el-table-column>
      <el-table-column
          prop="cpc"
          label="Cost"
          align="right"
          width="60"
          sortable>
      </el-table-column>
      <el-table-column
          prop="epc"
          label="EPC"
          width="60"
          align="right"
          sortable>
      </el-table-column>

      <el-table-column
          prop="conversion"
          label="Conversion"
          align="right"
          width="100"
          sortable>
      </el-table-column>
      <el-table-column
          prop="total_revenue"
          label="Total Revenue"
          align="right"
          width="150"
          sortable>
      </el-table-column>
      <el-table-column
          prop="profit"
          label="Profit"
          align="right"
          width="100"
          sortable>
      </el-table-column>
      <el-table-column
          prop="roi"
          label="Roi"
          align="right"
          width="100"
          sortable>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "CampaignData",
  data() {
    return {
      loading: false,
      buttonIndex: false,
    }
  },
  props: {
    campaigns: {
      type: Array
    }
  },

  created() {
   // this.loading=true
  },
  mounted() {
    this.loading= true
  },
  methods: {
    /**
     * event
     */
    mouseOver: function (index) {
      this.buttonIndex = index.id
    },
    mouseLeave: function () {
      this.buttonIndex = false
    }
  }

}
</script>

<style lang="scss" scoped>
@import "../../styles/panel-action";
@import "../../styles/loader";
.el-table .caret-wrapper {
  display: none !important;
}
</style>
