import CampaignLayout from "@/views/campaign/Layout"
import CampaignCreat from "@/views/campaign/Create"
import CampaignList from "@/views/campaign/List";

const campaignRouter = {
    path: '/campaign',
    name: "campaign",
    component: CampaignLayout,
    meta: {
        title: 'Campaign'
    },
    redirect: {name: 'campaign.list'},
    children: [
        {
            path: '', name: 'campaign.list', component: CampaignList
        },
        {
            path: 'create', name: 'campaign.create', component: CampaignCreat
        }
    ]
}
export default campaignRouter;