<template>
  <div>
    <div class="row margin-top-20">
      <div class="col-md-12">
        <div class="loader" v-if="loading"></div>
        <div class="portlet light bordered">
          <div class="portlet-title">
            <div class="caption font-red-sunglo">
              <span class="caption-subject bold uppercase">+ {{ titlePage }}</span>
            </div>
          </div>
          <div class="portlet-body form">
            <form class="form-horizontal" id="formLanding" @submit.prevent="handleSubmitLanding">
              <div class="form-body">
                <div class="form-group">
                  <label class="col-md-3 control-label">Title
                    <span class="required" aria-required="true">* </span>
                  </label>

                  <div class="col-md-6">
                    <input type="text" class="form-control" placeholder="Enter title"
                           @blur="validate('title')"
                           @keypress="validate('title')"
                           v-model="landingForm.title">
                  </div>
                  <p class="errors" v-if="!!errorForm.title">{{ errorForm.title }}</p>
                </div>
                <div class="form-group">
                  <span class="col-md-3 control-label">Type</span>
                  <div class="col-md-6">
                    <el-radio-group size="small" v-model="radioGroup">
                      <el-radio-button label="Landing" @change.native="handleClickType(2)"></el-radio-button>
                      <el-radio-button label="Pre Landing" @change.native="handleClickType(1)"></el-radio-button>
                    </el-radio-group>
                  </div>
                </div>
                <div class="form-group">
                  <label class="col-md-3 control-label">
                    Url
                    <span class="required" aria-required="true">* </span>
                  </label>
                  <div class="col-md-6">
                    <input type="text" class="form-control" placeholder="Enter url"
                           @blur="validate('url')"
                           @keypress="validate('url')"
                           v-model="landingForm.url">
                  </div>
                  <p class="errors" v-if="!!errorForm.url">{{ errorForm.url }}</p>
                </div>
                <div class="form-group">
                  <span class="col-md-3 control-label"></span>
                  <div class="col-md-6">
                    <a href="javascript:void(0)" class="label label-info mb-5 mr-5 cursor-pointer asis"
                       v-for="param in paramsUrl" :key="param"
                       @click.prevent="handleAddParam(param)">
                      <span>+{ {{ param }} }</span>
                    </a>
                  </div>
                </div>
                <div class="form-group">
                  <span class="col-md-3 control-label">Status</span>
                  <div class="col-md-6">
                    <el-select v-model="landingForm.status" placeholder="Choose status" size="small">
                      <el-option label="Active" :value="1"></el-option>
                      <el-option label="Inactive" :value="3"></el-option>
                    </el-select>
                  </div>
                  <p class="errors" v-if="!!errorForm.status">{{ errorForm.status }}</p>
                </div>
              </div>
              <div class="form-actions">
                <div class="row">
                  <div class="col-md-offset-3 col-md-9">
                    <button type="submit" class="btn btn-primary" @click="isClose=true">Save & Close</button>
                    <button type="submit" class="btn btn-primary">Save</button>
                    <button type="button" class="btn default" @click="$router.go(-1)">Cancel</button>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as Yup from "yup";
import {landingService} from "@/api/landing/landing.service";

const LandingSchema = Yup.object().shape({
  title: Yup.string().required("Title is required"),
  type: Yup.number().required("Type is required"),
  url: Yup.string().required("Url is required"),
  status: Yup.number().required("Status is required")
})
export default {
  name: "LandingCreate",
  data() {
    return {
      isClose: false,
      loading: false,
      countParam: 0,
      radioGroup: 'Landing',
      titlePage: '',
      landingIdUpdate: null,
      paramsUrl: ["os", "osversion", "country", "region", "campaignname"],
      landingForm: {
        title: '',
        type: 1,
        url: '',
        status: 1,
      },
      errorForm: {
        title: '',
        type: '',
        url: '',
      }
    }
  },
  created() {
    this.titlePage = this.$route.meta.title
    this.landingIdUpdate = this.$route.params.id;

    if (this.landingIdUpdate !== undefined) {
      this.handleGetLanding(this.$route.params.id)
    }
  },
  methods: {
    resetForm() {
      this.landingForm.url = ""
      this.landingForm.title = ""
    },
    /**
     * Function for create landing
     */
    handleClickType(value) {
      this.landingForm.type = value
    },
    handleAddParam(param) {
      let formatParam = "{hint}" + param + "={" + param + "}";
      let isMark = this.landingForm.url.includes("?")
      if (this.countParam <= 0 && !isMark) {
        formatParam = formatParam.replace("{hint}", "?");
      } else {
        formatParam = formatParam.replace("{hint}", "&");
      }
      if (this.landingForm.url != null && this.landingForm.url.length > 0) {
        this.landingForm.url += formatParam;
        this.countParam++;
      }
    },
    handleSubmitLanding() {
      LandingSchema.validate(this.landingForm, {abortEarly: false})
          .then(() => {
            landingService.createOrUpdateLanding(this.landingForm).then(response => {
              let data = response.data;
              let status = data["status"];
              let message = data["message"]
              if (status) {
               // eslint-disable-next-line no-undef
                Swal.fire({
                  icon: 'success',
                  title: message,
                  showConfirmButton: false,
                  timer: 1500
                }).then(() => {
                  if (this.isClose) {
                    this.$router.push("/landing/")
                  } else {
                    this.resetForm()
                  }

                })
              }
            })
          }).catch((err) => {
        err.inner.forEach((error) => {
          this.errorForm = {...this.errorForm, [error.path]: error.message};
        });
      });
    },
    /**
     * End function create
     */

    /***
     * Function for edit
     */
    handleGetLanding(landingId) {
      this.loading = true
      landingService.getLanding(landingId).then(response => {
        const data = response.data["data"];
        this.landingForm = data;
        const type = data.type
        if (type === 1) {
          this.radioGroup = "Pre Landing";
        } else {
          this.radioGroup = "Landing";
        }
        this.loading = false
        console.log("data", this.loading)
      })
    },
    /**
     * Validate form
     */
    validate(field) {
      LandingSchema.validateAt(field, this.landingForm)
          .then(() => (this.errorForm[field] = ""))
          .catch((err) => {
            this.errorForm[err.path] = err.message;
          });
    }
  }
}
</script>

<style lang="scss" scoped>

.asis {
  text-transform: none;
}

.mr-5 {
  margin-right: 5px !important;
}

.mb-5 {
  margin-bottom: 5px !important;
}

.label-info {
  background-color: #00bcd4;
  padding: 8px;
  color: white;
  font-style: italic;
}

.form-group {
  margin-bottom: 30px;
}

</style>
