<template>
  <div class="row">
    <div class="col-md-9">
      <router-link type="button" class="btn blue" :to="{path: 'create'}">
        <i class="fa fa-plus"></i> New
      </router-link>
      <router-link type="button" class="btn blue" v-bind:class="[activeOne]"
                   :to="{name: 'landing.edit', params: {id: landingId}}">
        <i class="fa fa-edit"></i> Edit
      </router-link>
      <button type="button" class="btn blue" v-bind:class="[activeMultiple]" @click="handleDeleteMultipleLanding">
        <i class="fa fa-trash"></i> Delete
      </button>
    </div>
  </div>
</template>

<script>
import {landingService} from "@/api/landing/landing.service";

export default {
  name: "FormAction",
  props: {
    activeOne: {
      type: String,
    },
    activeMultiple: {
      type: String,
    },
    landingId: {
      type: Number
    },
    listLandingId: {
      type: Array
    }
  },
  methods: {
    handleDeleteMultipleLanding(){
      // eslint-disable-next-line no-undef
      Swal.fire({
        title: 'Do you really want to delete it?',
        text: "Data will not be restored!",
        icon: 'warning',
        width: 420,
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, delete it!'
      }).then((result) => {
        if (result.isConfirmed) {
          landingService.deleteLanding( this.listLandingId).then(() => {
            // eslint-disable-next-line no-undef
            Swal.fire({
                  heightAuto: false,
                  width: 300,
                  icon: 'success',
                  title: 'Deleted',
                  showConfirmButton: false,
                  timer: 1000
                }
            ).then(
                () => {
                  this.$emit('completed');
                }
            )
          })
        }
      })

    }
  }
}
</script>

