<template>
  <div>
    <div class="loader" v-if="landings == null || landings.length < 0">
    </div>
    <el-table
        :data="landings"
        :header-cell-style="{ background: '#f5f7fa', color:'black',textAlign: 'center',    fontWeight: 'normal' }"
        height="600"
        @sort-change="sortChange"
        :header-row-style="{height:'55px'}"
        border
        style="width: 100%; margin-top: 20px"
        @selection-change="handleSelectChange"
        @cell-mouse-enter="mouseOver"
        @cell-mouse-leave="mouseLeave">
      <el-table-column
          type="selection"
          width="30"
          align="center">
      </el-table-column>
      <el-table-column
          prop="id"
          label="Id"
          width="60"
          sortable="custom"
          align="center">
      </el-table-column>
      <el-table-column
          prop="title"
          label="Title"
          align="left">
        <template slot-scope="scope">
          <div class="panel-action position-relative">
            <div class="name"> {{ scope.row.title }}</div>
            <ul class="position-absolute right-0 top-1 display"
                v-show="buttonIndex === scope.row.id">
              <li>
                <router-link :to="{name: 'landing.edit', params: {id: scope.row.id}}">
                  <i class="fa fa-edit blue"></i>
                </router-link>
              </li>
              <li>
                <a href="javascript:void(0)" @click="handleDelete(scope.row.id)">
                  <i class="fa fa-trash blue"></i>
                </a>
              </li>
            </ul>
          </div>
        </template>
      </el-table-column>
      <el-table-column
          prop="url"
          label="Page Url"
          width="200"
          align="left">
        <template slot-scope="scope">
          <div class="ellipsis">
            <span>{{ scope.row.url }}</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column
          prop="type_label"
          label="Type"
          width="100"
          align="left">
        <template slot-scope="scope">
          <div>
            <span style=" text-transform: capitalize;">{{ scope.row.type_label }}</span>
          </div>
        </template>
      </el-table-column>
      <!--      <el-table-column-->
      <!--          prop="status_label"-->
      <!--          label="Status"-->
      <!--          align="center"-->
      <!--          width="100">-->
      <!--        <template slot-scope="scope">-->
      <!--          <el-tag-->
      <!--              :type="scope.row.status_label === 'active' ? 'primary' : 'success'">-->
      <!--            {{ scope.row.status_label }}-->
      <!--          </el-tag>-->
      <!--        </template>-->
      <!--      </el-table-column>-->
    </el-table>
  </div>
</template>

<script>
import {landingService} from "@/api/landing/landing.service";
export default {
  name: "FormData",
  data() {
    return {
      loading: false,
      buttonIndex: false,
      multipleSelection: [],
    }
  },
  props: {
    landings: {
      type: Array
    }
  },
  methods: {
    handleDelete(id) {
      // eslint-disable-next-line no-undef
      Swal.fire({
        title: 'Do you really want to delete it?',
        text: "Data will not be restored!",
        icon: 'warning',
        width: 420,
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, delete it!'
      }).then((result) => {
        if (result.isConfirmed) {
          landingService.deleteLanding(id).then(() => {
            // eslint-disable-next-line no-undef
            Swal.fire({
                  heightAuto: false,
                  width: 300,
                  icon: 'success',
                  title: 'Deleted',
                  showConfirmButton: false,
                  timer: 1000
                }
            ).then(
                () => {
                  this.$emit('completed');
                }
            )
          })
        }
      })
    },
    /**
     * Select change
     */
    handleSelectChange(val) {
      this.multipleSelection = val;
      let listLanding = this.multipleSelection
      let activeOne = 'disabled'
      let activeMultiple = 'disabled'
      let lengthSelection = this.multipleSelection.length
      if (lengthSelection <= 1 && lengthSelection > 0) {
        activeOne = ''
      }
      if (lengthSelection > 0) {
        activeMultiple = ''
      }
      let objectSelect = {
        activeOne: activeOne,
        activeMultiple: activeMultiple,
        listLanding: listLanding
      }
      this.$emit('handle-landing-select', objectSelect)
    },

    /**
     * event
     */
    mouseOver: function (index) {
      this.buttonIndex = index.id
    },
    mouseLeave: function () {
      this.buttonIndex = false
    },
    /**
     * Sort
     */
    sortChange(data) {
      let sortType = 'ASC';
      const {prop, order} = data;
      this.$emit("update:sort_field", prop)
      if (order === "descending") {
        sortType = 'DESC'
      }
      this.$emit("update:sort_type", sortType)
      this.$emit('completed', {});
    },
  }
}
</script>

<style lang="scss" scoped>
@import "../../../styles/panel-action";

.el-table--enable-row-hover .el-table__body tr:hover > td {
  background-color: red !important;
}

.el-table__body tr.el-table__row.hover-row > td {
  background-color: red !important;
}
</style>