import OfferSourceLayout from '@/views/offer-source/Layout'
import OfferSourceCreate from '@/views/offer-source/Create'
import OfferSourceDetail from '@/views/offer-source/Detail'

const offerSourceRouter = {
    path: '/networks',
    name: "network",
    component: OfferSourceLayout,
    meta: {
        title: 'Offer Sources'
    },
    redirect: {name: 'network.detail'},
    children: [
        {
            path: '', name: 'network.detail', component: OfferSourceDetail
        },
        {
            path: 'create', name: 'network.create', component: OfferSourceCreate, meta: {title: 'New Offer Source'}
        },
        {
            path: ':id/edit', name: 'network.edit', component: OfferSourceCreate, meta: {title: 'Update Offer Source'}
        }
    ]
}

export default offerSourceRouter;