<template>
  <!-- BEGIN HEADER -->
  <div class="page-header navbar navbar-fixed-top">
    <!-- BEGIN HEADER INNER -->
    <div class="page-header-inner">
      <!-- BEGIN LOGO -->
      <div class="page-logo">
        <a href="/">
          <img src="@/assets/img/logo.png" alt="logo" class="logo-default"/>
        </a>
        <div class="menu-toggler sidebar-toggler hide">
        </div>
      </div>
      <!-- END LOGO -->
      <!-- BEGIN RESPONSIVE MENU TOGGLER -->
      <a href="javascript:;" class="menu-toggler responsive-toggler" data-toggle="collapse"
         data-target=".navbar-collapse">
      </a>
      <!-- END RESPONSIVE MENU TOGGLER -->
      <!-- BEGIN TOP NAVIGATION MENU -->
      <div class="top-menu">
        <ul class="nav navbar-nav pull-right">
          <!-- BEGIN NOTIFICATION DROPDOWN -->
          <!-- BEGIN USER LOGIN DROPDOWN -->
          <!-- DOC: Apply "dropdown-dark" class after below "dropdown-extended" to change the dropdown styte -->
          <li class="dropdown dropdown-user" v-if="getAccountLoggedIn">
            <a href="javascript:;" class="dropdown-toggle" data-toggle="dropdown" data-hover="dropdown"
               data-close-others="true">
              <span class="username username-hide-on-mobile" style="color: white;font-weight: bold;">
					{{ username }} </span>
              <i class="fa fa-angle-down"></i>
            </a>
            <ul class="dropdown-menu dropdown-menu-default">
              <li>
                <router-link :to="{name: 'profile'}">
                  <i class="icon-user"></i> My Profile
                </router-link>
              </li>

              <li class="divider">
              </li>
              <li>
                <a href="/login">
                  <i class="icon-key"></i> Log Out </a>
              </li>
            </ul>
          </li>
          <!-- END USER LOGIN DROPDOWN -->
        </ul>
      </div>
      <!-- END TOP NAVIGATION MENU -->
    </div>
    <!-- END HEADER INNER -->
  </div>
  <!-- END HEADER -->
</template>

<script>
import {profileService} from "@/api/profile/profile.service";
import {mapGetters} from "vuex";

export default {
  name: "HeaderLayout",
  data() {
    return {
      username: '',
    }
  },
  computed: {
    ...mapGetters('account', ['getAccountLoggedIn'])
  },
  created() {
    this.handleUserInfo()
  },
  methods: {
    async handleUserInfo() {
      const {data} = await profileService.infoUser()
      if(data.status){
        let user = data["data"]
        let first_name = user.first_name
        let last_name = user.last_name
        this.username = first_name + " " + last_name
      }
    },
  }
}


</script>

<style scoped>

</style>
