<template>
  <div class="container mt-3">
    <form role="form">
      <div class="row">
        <div class="col-md-offset-2">
          <div class="form-group col-md-6">
            <label class="control-label">First Name</label>
            <input type="text" placeholder="Nam" class="form-control" v-bind:class="isSpinner"
                   v-model="profileForm.first_name">
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-md-offset-2">
          <div class="form-group col-md-6">
            <label class="control-label">Last Name</label>
            <input type="text" placeholder="Nguyen" class="form-control" v-bind:class="isSpinner"
                   v-model="profileForm.last_name">
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-md-offset-2">
          <div class="form-group col-md-6">
            <label class="control-label">Email</label>
            <input type="text" placeholder="nam.nt@bidgear.com" class="form-control" v-bind:class="isSpinner"
                   v-model="profileForm.email" disabled>
          </div>
        </div>
      </div>

      <div class="row">
        <div class="col-md-offset-2">
          <div class="form-group col-md-6">
            <a href="javascript:;" class="btn green-haze" @click="handleUserUpdate">
              Update Profile </a>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import {profileService} from "@/api/profile/profile.service";

export default {
  name: "UserPersonInfo",
  data() {
    return {
      isSpinner: 'spinner',
      profileForm: {
        first_name: '',
        last_name: '',
        email: ''
      },
    }
  },
  created() {
    this.handleUserInfo()
  },
  methods: {
    /**
     * Get user
     *
     */
    async handleUserInfo() {
      const {data} = await profileService.infoUser()
      this.profileForm = data["data"]
      this.isSpinner = ''
    },
    /**
     * Update user
     */
    handleUserUpdate() {
      const user = this.profileForm
      profileService.updateUser(user).then(response => {
        let data = response.data;
        let status = data["status"];
        let classError ='';
        if (status) {
           classError = 'alert-success'
        }else{
          classError = 'alert-danger'
        }
        this.$emit('info-update-user', data["message"], classError)
      })
    },

  }
}
</script>

