import configs from "@/helpers/configs";
import {authHeader} from "@/helpers/auth-header";
import axios from 'axios';

export const offerSourceService = {
    createOrUpdateOfferSource,
    getOfferSource,
    getListOfferSource,
    deleteOfferSource
}

function createOrUpdateOfferSource(offerSource) {
    let url = configs.apiUrl + 'offer-source/create'
    let bodyFormData = new FormData();
    if (offerSource.id !== undefined) {
        url = configs.apiUrl + 'offer-source/update'
    }
    const keys = Object.keys(offerSource);
    for (let i = 0; i < keys.length; i++) {
        let key = keys[i]
        let value = offerSource[key]
        bodyFormData.append(key, value);
    }
    return axios({
        method: 'POST',
        timeout: 1000 * 60,
        url: url,
        headers: authHeader(),
        data: bodyFormData

    }).then(response => {
        console.log(response.data)
        return response
    });
}

function getOfferSource(sourceId) {
    return axios({
        method: 'GET',
        timeout: 1000 * 60,
        url: configs.apiUrl + "offer-source/get?id=" + sourceId,
        headers: authHeader(),
        data: {}
    })
}

function getListOfferSource(query) {
    return axios({
        method: "GET",
        timeout: 1000 * 60,
        url: configs.apiUrl + "offer-source/list?",
        headers: authHeader(),
        params: query
    }).then(response => {
        return response
    })
}

function deleteOfferSource(argsId) {
    let bodyFormData = new FormData();
    bodyFormData.append("id", argsId);
    return axios({
        method: 'POST',
        timeout: 1000 * 60,
        url: configs.apiUrl + "offer-source/delete?",
        headers: authHeader(),
        data: bodyFormData

    }).then(response => {
        console.log(response.data)
        return response
    });
}
