<template>
  <div class="forgot">
    <div class="content">
      <HeaderError v-if="statusForgot" :error-class="errorClass" :error-message="errorMessage"/>
      <!-- BEGIN FORGOT PASSWORD FORM -->
      <form class="forget-form" novalidate="novalidate" style="display: block;" @click.prevent="handleForgot">
        <h3>Forget Password ?</h3>
        <p>
          Enter your e-mail address below to reset your password.
        </p>
        <div class="form-group">
          <input class="form-control placeholder-no-fix" type="text" autocomplete="off" placeholder="Email"
                 name="email" v-model="forgotForm.email"
                 @blur="validate('email')"
                 @keypress="validate('email')">

          <p class="errors" v-if="!!errorsForgot.email">{{ errorsForgot.email }}</p>
        </div>

        <div class="form-actions">
          <button type="button" id="back-btn" class="btn btn-default" @click="$router.go(-1)">Back</button>
          <button type="submit" class="btn btn-success uppercase pull-right">Submit</button>
        </div>
      </form>
      <!-- END FORGOT PASSWORD FORM -->
    </div>
  </div>
</template>

<script>
import {userService} from "@/api/user/user.service";
import HeaderError from "@/components/HeaderError";
import * as Yup from "yup";

const ForgotSchema = Yup.object().shape({
      email: Yup.string().email("Must be a valid email").required('Email is request')
    }
);
export default {
  name: "AuthForgot",
  components: {
    HeaderError
  },
  data() {
    return {
      statusForgot: false,
      errorClass: 'alert-danger',
      errorMessage: '',
      forgotForm: {
        email: '',
      },
      errorsForgot: {
        email: ''
      }
    }
  },
  methods: {
    handleForgot() {
      console.log("Demo")
      ForgotSchema.validate(this.forgotForm, {abortEarly: false})
          .then(() => {
            userService.resetPassword(this.forgotForm.email).then(response => {
              let data = response.data;
              let status = data["status"];
              if (status) {
                console.log("forgot response..", status);
                // eslint-disable-next-line no-undef
                Swal.fire({
                      title: 'Reset password success',
                      text: 'New password has been sent to your email, please check your email!',
                      icon: 'info',
                      confirmButtonText: 'Back Login',
                    }
                ).then((result) => {
                  if (result.isConfirmed) {
                    // Back to login
                    this.$router.push("/login");
                  }
                })
              } else {
                this.statusForgot = true
                this.errorMessage = data["message"];
              }

            })
          })
          .catch((err) => {
            err.inner.forEach((error) => {
              this.errorsForgot = {...this.errorsForgot, [error.path]: error.message};
            });
          });
    },
    validate(field) {
      ForgotSchema.validateAt(field, this.forgotForm)
          .then(() => (this.errorsForgot[field] = ""))
          .catch((err) => {
            this.errorsForgot[err.path] = err.message;
          });
    }
  },
  mounted() {
    console.log("Name....", this.$route.params.token)
  },
}
</script>

<style lang="scss" scoped>
.errors {
  color: red;
}

.forgot {
  .content {
    background-color: #eceef1;
    -webkit-border-radius: 7px;
    -moz-border-radius: 7px;
    border-radius: 7px;
    width: 400px;
    margin: 40px auto 10px auto;
    padding: 30px;
    padding-top: 10px;
    overflow: hidden;
    position: relative;

    h3 {
      color: #4db3a5;
      text-align: center;
      font-size: 28px;
      font-weight: 400 !important;
    }

    .form-control {
      background-color: #dde3ec;
      height: 43px;
      color: #8290a3;
    }

    .form-actions {
      .btn-default:hover {
        background-color: #fafaff;
        color: #45b6af;
      }

      .btn-default {
        font-weight: 600;
        padding: 10px 25px !important;
        color: #6c7a8d;
        background-color: #ffffff;
        border: none;
      }

      .btn-success:hover, .btn-success:focus, .btn-success:active, .btn-success.active {
        color: #fff;
        border-color: #307f7a;
      }

      .btn-success {
        background-color: #3b9c96;
        font-weight: 600;
        padding: 10px 20px !important;
      }
    }
  }
}
</style>
