const state = {
    type: null,
    message: null,
    isLoading: false
};

const actions = {
    success({commit}, message) {
        commit('success', message);
    },
    error({commit}, message) {
        commit('error', message);
    },
    clear({commit}) {
        commit('clear');
    }
};

const getters = {
    getErrorType: state => {
        return state.type
    },
    getErrorMessage: state => {
        console.log("message...", state.message)
        return state.message
    },
    getLoading: state => {
        return state.isLoading
    }
};

const mutations = {
    success(state, message) {
        state.type = 'alert-success';
        state.message = message[0];
    },
    error(state, message) {
        state.type = 'alert-danger';
        state.message = message[0];
    },
    clear(state) {
        state.type = null;
        state.message = null;
    }
};


export const alert = {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
};
