<template>
  <div>
    <div class="row margin-top-20">
      <div class="col-md-12">
        <div class="loader" v-if="loading"></div>
        <div class="portlet light bordered">
          <div class="portlet-title">
            <div class="caption font-red-sunglo">
              <span class="caption-subject bold uppercase">+ {{ titlePage }}</span>
            </div>
          </div>
          <div class="portlet-body form">
            <form class="form-horizontal" @submit.prevent="handleSubmitOfferSource">
              <div class="form-body">
                <div class="form-group">
                  <label class="col-md-3 control-label">Title
                    <span class="required" aria-required="true">* </span>
                  </label>
                  <div class="col-md-6">
                    <input type="text" class="form-control" placeholder="Enter title"
                           @blur="validate('title')"
                           @keypress="validate('title')"
                           v-model="offerSourceForm.title">
                  </div>
                  <p class="errors" v-if="!!errorForm.title">{{ errorForm.title }}</p>
                </div>

                <div class="form-group">
                  <label class="col-md-3 control-label">Status
                    <span class="required" aria-required="true">* </span>
                  </label>
                  <div class="col-md-6">
                    <el-select v-model="offerSourceForm.status" placeholder="Choose status" size="small">
                      <el-option label="Active" :value="1"></el-option>
                      <el-option label="Inactive" :value="2"></el-option>
                    </el-select>
                  </div>
                  <p class="errors" v-if="!!errorForm.status">{{ errorForm.status }}</p>
                </div>

              </div>
              <div class="form-actions">
                <div class="row">
                  <div class="col-md-offset-3 col-md-9">
                    <button type="submit" class="btn btn-primary" @click="isClose=true">Save & Close</button>
                    <button type="submit" class="btn btn-primary">Save</button>
                    <button type="button" class="btn default" @click="$router.go(-1)">Cancel</button>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as Yup from "yup";
import {offerSourceService} from "@/api/offer-source/offer-source.service";

const OfferSourceSchema = Yup.object().shape({
  title: Yup.string().required("Title is required"),
  status: Yup.string().required("Status is required")
})
export default {
  name: "OfferSourceCreate",
  data() {
    return {
      isClose: false,
      loading: false,
      titlePage: '',
      offerSourceIdUpdate: null,
      offerSourceForm: {
        title: '',
        status: 1,
      },
      errorForm: {
        title: '',
      }
    }
  },
  created() {
    this.titlePage = this.$route.meta.title
    this.offerSourceIdUpdate = this.$route.params.id;

    if (this.offerSourceIdUpdate !== undefined) {
      this.handleGetOfferSource(this.$route.params.id)
    }
  },
  methods: {
    resetForm() {
      this.offerSourceForm.title = ""
    },
    /**
     * Function for create offer source
     */
    handleSubmitOfferSource() {
      OfferSourceSchema.validate(this.offerSourceForm, {abortEarly: false})
          .then(() => {
            offerSourceService.createOrUpdateOfferSource(this.offerSourceForm, this.offerSourceIdUpdate)
                .then(response => {
                  let data = response.data;
                  let status = data["status"];
                  let message = data["message"]
                  if (status) {
                    // eslint-disable-next-line no-undef
                    Swal.fire({
                      icon: 'success',
                      title: message,
                      showConfirmButton: false,
                      timer: 1500
                    }).then(() => {
                      if (this.isClose) {
                        this.$router.push("/networks/")
                      } else {
                        this.resetForm()
                      }

                    })
                  }
                })
          }).catch((err) => {
        err.inner.forEach((error) => {
          this.errorForm = {...this.errorForm, [error.path]: error.message};
        });
      });
    },
    /**
     * End function create
     */

    /***
     * Function for edit
     */
    handleGetOfferSource(sourceId) {
      this.loading = true
      offerSourceService.getOfferSource(sourceId).then(response => {
        this.offerSourceForm = response.data["data"];
        this.loading = false
        console.log("data", this.loading)
      })
    },
    /**
     * Validate form
     */
    validate(field) {
      OfferSourceSchema.validateAt(field, this.offerSourceForm)
          .then(() => (this.errorForm[field] = ""))
          .catch((err) => {
            this.errorForm[err.path] = err.message;
          });
    }
  }
}
</script>

