import configs from "@/helpers/configs";
import {authHeader} from "@/helpers/auth-header";
import axios from 'axios';

export const landingService = {
    createOrUpdateLanding,
    getLanding,
    getListLanding,
    deleteLanding
}

function createOrUpdateLanding(landing) {
    let url = configs.apiUrl + 'landing/create'
    let bodyFormData = new FormData();
    if (landing.id !== undefined) {
        url = configs.apiUrl + 'landing/update'
    }
    const keys = Object.keys(landing);
    for (let i = 0; i < keys.length; i++) {
        let key = keys[i]
        let value = landing[key]
        bodyFormData.append(key, value);
    }
    return axios({
        method: 'POST',
        timeout: 1000 * 60,
        url: url,
        headers: authHeader(),
        data: bodyFormData

    }).then(response => {
        console.log(response.data)
        return response
    });
}

function getLanding(landingId) {
    return axios({
        method: 'GET',
        timeout: 1000 * 60,
        url: configs.apiUrl + "landing/get?id=" + landingId,
        headers: authHeader(),
        data: {}
    })
}

function getListLanding(query) {
    return axios({
        method: "GET",
        timeout: 1000 * 60,
        url: configs.apiUrl + "landing/list?",
        headers: authHeader(),
        params: query
    }).then(response => {
        return response
    })
}

function deleteLanding(argsId) {
    let bodyFormData = new FormData();
    bodyFormData.append("id", argsId);
    return axios({
        method: 'POST',
        timeout: 1000 * 60,
        url: configs.apiUrl + "landing/delete?",
        headers: authHeader(),
        data: bodyFormData
    }).then(response => {
        console.log(response.data)
        return response
    });
}
