<template>
  <div>
    <div class="row margin-top-20">
      <div class="col-md-12">
        <div class="portlet light bordered">
          <div class="portlet-title">
            <div class="caption font-red-sunglo">
              <i class="icon-settings font-red-sunglo"></i>
              <span class="caption-subject bold uppercase"> Landings</span>
            </div>
          </div>
          <div class="portlet-title">
            <form-action :landingId="this.listIdLanding.length > 0 ? parseInt(this.listIdLanding[0]): -1"
                         :listLandingId="this.listIdLanding"
                         :activeMultiple="activeMultiple" :activeOne="activeOne"
                         @completed="handleListLanding"/>
          </div>
          <div class="portlet-title">
            <form-search :title.sync="listQuery.title" :status.sync="listQuery.status"
                         :type.sync="listQuery.type"
                         :start_date.sync="listQuery.start_date"
                         :end_date.sync="listQuery.end_date"
                         @completed="handleListLanding"/>

          </div>
          <div class="loader" v-if="loading"></div>
          <form-data :landings="landings" @handle-landing-select="handleLandingSelect"
                     :sort_field.sync="listQuery.sort_field"
                     :sort_type.sync="listQuery.sort_type"
                     @completed="handleListLanding"/>
        </div>
      </div>

    </div>

    <div class="card-body">
      <pagination v-show="totalLanding>0" :total="totalLanding" :page.sync="listQuery.page"
                  :limit.sync="listQuery.per_page"
                  @pagination="handleListLanding"/>
    </div>
  </div>
</template>

<script>
import FormData from "@/views/landing/partials/FormData"
import Pagination from "@/components/Pagination";
import FormAction from "@/views/landing/partials/FormAction"
import FormSearch from "@/views/landing/partials/FormSearch";
import {landingService} from "@/api/landing/landing.service";

export default {
  name: "LandingDetail",
  components: {
    FormData,
    FormAction,
    FormSearch,
    Pagination
  },
  data() {
    return {
      activeOne: 'disabled',
      activeMultiple: 'disabled',
      loading: false,
      listQuery: {
        page: 1,
        per_page: 20,
        start_date: '',
        end_date: '',
        date: '',
        sort_field: 'id',
        sort_type: 'DESC',
        title: '',
        status: '',
        type: '',
      },
      totalLanding: 0,
      listIdLanding: [],
      landings: null,
    }
  },
  created() {
    this.handleListLanding()
  },
  methods: {
    handleListLanding() {
      this.loading = true;
      landingService.getListLanding(this.listQuery).then(response => {
        let data = response.data;
        this.landings = data["data"];
        this.totalLanding = parseInt(data["total"]);
        this.loading = false
      })
    },
    handleLandingSelect(objectSelect) {
      this.listIdLanding = [];
      this.activeOne = objectSelect.activeOne;
      this.activeMultiple = objectSelect.activeMultiple;
      const listLanding = objectSelect.listLanding;
      listLanding.forEach((element) => {
        this.listIdLanding.push(element.id);
      })
    }

  }
}
</script>

<style lang="scss" scoped>
</style>
